<template>
  <div class="MainboardAdd">
    <v-dialog v-model="dialog" width="80%">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

        <v-card>
          <v-card-title :class="titleStyle" primary-title
            >{{ title }}
            <v-spacer></v-spacer>
            <v-icon @click="dialog = !dialog">mdi-close</v-icon>
          </v-card-title>

          <v-card-text>
            <v-form ref="form">
              <v-row>
                <v-col cols="4">
                  <v-text-field
                    type="date"
                    label="日期"
                    v-model="list.date"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                    label="公告內容"
                    v-model="list.content"
                    :rules="emptyRules"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              class="primary"
              @click="addOK"
              v-if="processType == 'add'"
              :loading="isloading"
              :disabled="isloading"
              >確認新增</v-btn
            >
            <v-btn class="success" @click="editOK" v-if="processType == 'edit'"
              >確認修改</v-btn
            >
          </v-card-actions>
        </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {},
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      isloading: false,
    };
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增公告";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {
        date: dayjs().format("YYYY-MM-DD"),
        show:true,
        readed:[],
      };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    }, 
    //進入畫面時,執行的是修改作業
    editProcess(item) {
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.list = { ...item }; //複制一份實際修改資料內容

      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.isloading = true;
        this.list.create_man = this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")";

        let postData = {
          datalist: JSON.stringify(this.list),
        };  

        this.$api.options(
          "general/add/" + this.$store.state.databaseName + "/mainboard",
          postData
        ).then((rs) => {
          if (rs.state == 1) {
            var pop = { msg: "已新增", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.$store.dispatch("getUsingItems", "mainboard");
            this.dialog= this.isloading = false;
          }
        });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        if (!this.list.edit_man){this.list.edit_man = ""}
        this.list.edit_man = this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")" + this.list.edit_man;

        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man: "",  //伺服端要有這個不然不能改.^^
        };  

        this.$api.options(
          "general/edit/" + this.$store.state.databaseName + "/mainboard",
          postData
        ).then((rs) => {
          if (rs.state == 1) {
            var pop = { msg: "已修改", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.$store.dispatch("getUsingItems", "mainboard");
            this.dialog = false;
          }
        });
      }else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
  },
};
</script>
