<template>
  <div class="mainboardlist">
    <v-card>
      <v-card-title
        v-if="viewType != 'inFunction'"
        class="d-flex justify-space-between"
      >
        <div class="font-weight-black">公告欄</div>
        <v-checkbox
          label="顯示公告欄設定"
          v-model="$store.state.cData.showMainboard"
          class="font-weight-black"
          color="error"
          hide-details
          @change="changeShowMainboard"
        ></v-checkbox>
        <MainboardAdd ref="editProcess" />
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text>
        <v-alert
          color="primary"
          outlined
          v-for="item in items"
          :key="item.snkey"
        >
          <h2 class="d-flex justify-space-between">
            <span>公告日期: {{ item.date }}</span>
            <v-checkbox
              label="顯示/不顯示"
              v-model="item.show"
              hide-details
              class="pa-0 ma-0 mb-1"
              @change="setShow(item)"
              v-if="viewType != 'inFunction'"
            ></v-checkbox>
            <div class="mb-2" v-if="viewType != 'inFunction'">
              <v-btn class="success mx-1" @click.stop="edit(item)">修改</v-btn>
              <v-btn class="error" @click.stop="del(item)">刪除</v-btn>
            </div>
            <v-btn
              class="red lighten-2 white--text"
              @click.stop="read(item)"
              v-if="filterReaded(item) && viewType == 'inFunction'"
              >已讀</v-btn
            >
          </h2>

          <v-divider class="my-2"></v-divider>

          <h3 class="mt-2" style="white-space:pre-wrap">{{ item.content }}</h3>

          <v-divider class="my-2"></v-divider>
          <h3 class="red--text">
            已讀人員:
            <v-chip
              class="ma-2"
              color="red"
              outlined
              v-for="readed,index in item.readed"
              :key="index"
            >
              {{ readed.username }}-{{ readed.time }}
            </v-chip>
          </h3>
        </v-alert>
        <div v-if="items.length == 0">暫無資料...</div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import dayjs from "dayjs";
import MainboardAdd from "@/views/main/MainboardAdd";

export default {
  props: ["viewType"], //viewType是inFunction時,代表是從Function打開的公佈欄界面
  components: { MainboardAdd },
  data() {
    return {};
  },

  computed: {
    items() {
      let match = this.$store.state.usingItems.map((i) => {
        let newItem = JSON.parse(i.datalist);
        newItem.snkey = i.snkey;
        return newItem;
      });
      match = match.sort((a, b) => {
        return a.date < b.date ? 1 : -1;
      });

      //判斷是不是顯示在Function的內容 -> show 才顯示
      if (this.viewType == "inFunction") match = match.filter((i) => i.show);

      return match; //實際輸出結果
    },
    searchfilter() {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
  },

  mounted() {
    this.$store.dispatch("getUsingItems", "mainboard"); //讀入本頁使用的資料
  },

  methods: {
    //點選進入修改功能 -> 傳資料到修改畫面
    edit(item) {
      this.$refs.editProcess.$emit("fromParent", item);
    },
    del(delData) {
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "mainboard",
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.options(
            "general/delv3/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename,
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "已刪除", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.$store.dispatch("getUsingItems", "mainboard"); //讀入本頁使用的資料
            }
          });
        }
      });
    },
    setShow(item) {
      let postData = {
        snkey: item.snkey,
        datalist: JSON.stringify(item),
        edit_man: "", //伺服端要有這個不然不能改.^^
      };
      this.$api.options(
        "general/edit/" + this.$store.state.databaseName + "/mainboard",
        postData
      ).then((rs) => {
        if (rs.state == 1) {
          var pop = { msg: "已修改", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
          this.$store.dispatch("getUsingItems", "mainboard");
        }
      });
    },
    //更動顯示公告欄設定
    changeShowMainboard() {
      let showMainboard = "";
      if (this.$store.state.cData.showMainboard) {
        showMainboard = true;
      }
      let postData = {
        snkey: "1",
        showMainboard: showMainboard,
        edit_man: "",
      };

      this.$api.options(
        "general/edit/" + this.$store.state.databaseName + "/other_data",
        postData
      ).then((rs) => {
        if (rs.state == 1) {
          var pop = { msg: "已修改", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
        }
      });
    },
    read(item) {
      if (!item.readed) {
        item.readed = [];
      }

      item.readed.unshift({
        snkey: this.$store.state.pData.snkey,
        username: this.$store.state.pData.username,
        time: dayjs().format("YYYY-MM-DD HH:mm:ss"),
      });

      let postData = {
        snkey: item.snkey,
        datalist: JSON.stringify(item),
        edit_man: "", //伺服端要有這個不然不能改.^^
      };

      this.$api.options(
        "general/edit/" + this.$store.state.databaseName + "/mainboard",
        postData
      ).then((rs) => {
        if (rs.state == 1) {
          var pop = { msg: "已讀", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
          this.$store.dispatch("getUsingItems", "mainboard");
        }
      });
    },
    filterReaded(item) {
      if (!item.readed) {
        return false;
      }
      let match = item.readed.find(
        (i) => i.snkey == this.$store.state.pData.snkey
      );

      if (match) {
        return false;
      } else {
        return true;
      }
    },
  },
};
</script>

